            <el-form-item>

                <!-- <el-form-item style="margin-left: 50px;">
                <el-input placeholder="请输入设备/场地名称" v-model="deviceForm.orderPlace" suffix-icon="el-icon-search"></el-input>
             </el-form-item> -->
