                    <el-select v-model="deviceForm.orderPlace" @change="getAllSquare2" placeholder="请选择预约设备/场地"
                        style="height:0.22rem; font-size:0.07rem;">
                        <el-option v-for="item in orderPlace" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <!-- <el-button @click="load2" type="primary">查询</el-button> -->
                <el-button type="warning" @click="reset">重置</el-button>
            </el-form-item>
        </el-form>


        <!-- 主要表格 -->
        <el-table :data="tableData" style="width: 100%" border stripe>

