                <el-table-column  :key="item.label" :prop="item.prop"  :label="item.label" align="center">
               
                </el-table-column>
                    
            </template> -->

            <!-- <el-table-column prop="timePeriod" label="预约时间" width="150" align="center">
                <template slot-scope="scope">
                    {{ formatTimePeriod(scope.row.timePeriod) }}
                </template>
            </el-table-column> -->
            <!-- <el-table-column prop="submitTime" label="提交时间" type="datetime" width="170" align="center"></el-table-column> -->
            <!-- <el-table-column prop="enabled" label="是否确认" align="center" width="80">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.enabled" active-color="#13ce66" inactive-color="#ccc"
                        @change="changeEnable(scope.row)"></el-switch>
                </template>
            </el-table-column> -->
            <!-- <el-table-column prop="status" label="审核状态" width="120" align="center">
                <template slot-scope="scope">
                    <el-tag type="success" v-if="scope.row.status === '审核通过'">审核通过</el-tag>
                    <el-tag type="warning" v-if="scope.row.status === '等待审核'">等待审核</el-tag>
                    <el-tag type="danger" v-if="scope.row.status === '审核不通过'">审核不通过</el-tag>
                </template>
            </el-table-column> -->
            <!-- <el-table-column prop="reason" label="审核意见" width="170" align="center" show-overflow-tooltip=""></el-table-column> -->

            <!-- <el-table-column label="操作" width="200" align="center">
                <template slot-scope="scope">
                   // :disabled="scope.row.status === '审核通过'"
                    <el-button style="margin-left:5px" size="mini" type="success"  @click="handleAudit(scope.row)">审核
                    </el-button>
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">删除
                    </el-button>
                </template>
            </el-table-column> -->
        </el-table>

        <!-- 页码 -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
