</template>

<script>
export default {
    data() {
        return {
            dialogAduitVisible: false,
            tableData: [],
            equipData: [],
            modelType: 0,//0表示新增的弹窗，1表示编辑,
            multipleSelection: [],
            deviceForm: {
                type: "third",
                orderPlace: "",
            },
            total: 0,//当前的总条数
            pageNum: 1,
            pageSize: 10,

            selectedOrderPlace: "",
            orderPlace: [],

            rules: {
                //验证用户名是否合法

            },
        }
    },
    created() {
        this.load()
        // this.getAllEquip()
        this.getAllSquare2()
    },
    methods: {
        //获取所有设备
        // getAllEquip(){
        //     this.request.get("/square/type", {
        //     params: {
        //     type: this.type
        //     }
        //     }).then(res => {
        //         console.log(res)
        //         this.equipData = res.data;
        //     });
        // },
        getAllSquare2() {
            console.log("aaaa1")
            this.request.get("/square/type", {
                params: {
                    type: this.deviceForm.type
                }
            }).then(res => {
                // console.log("hideField")
                // console.log(res)
                this.orderPlace = res.data;
                console.log("aaaa" + this.orderPlace)
                // this.isOrderCenterSelected = true;
                this.load();
                this.selectedOrderPlace = this.deviceForm.orderPlace;
            });
        },
        handleClose() {
            this.$refs.form.resetFields()
            this.dialogAduitVisible = false
            this.load()
        },
        cancel() {
            this.handleClose()
        },
        handleAudit(row) {
            this.form = row
            this.dialogAduitVisible = true

        },

        indexMethod(index) {
            let curpage = this.pageNum   //单前页码，具体看组件取值
            let limitpage = this.pageSize   //每页条数，具体是组件取值
            return (index + 1) + (curpage - 1) * limitpage
        },
        handleDelete(id) {
            this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.delete("/equipmentOrder/back/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("请先选择需要删除的内容");
                return;
            }

            let ids = this.multipleSelection.map(v => v.id)  //将对象数组[{},{}...]变成纯id的数组[1.2.3]
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.post("/equipmentOrder/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '批量删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.pageNum = 1
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消批量删除'

                });
                this.load()
            });
        },
        // changeEnable(row) {
        //     // console.log("1111")
        //     // this.request.post("/equipmentOrder", row).then(res => {
        //     this.request.post("/equipmentOrder/update", row).then(res => {

        //         if (res.code === '200') {
        //             this.$message.success("确认修改成功")
        //         } else if(res.code === '402'){  // 确认预约冲突设置
        //             this.$message.warning("已存在相同的预约，不可确认预约")
        //             row.enabled = false
        //         }
        //         else {
        //             this.$message.error("确认修改失败")
        //         }
        //     })
        // },
        queryRange() {
            
            this.pageNum = 1
            this.load();
        },
        load2() {
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/eqipUseage/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    type: this.deviceForm.type,
                    // orderDate: this.deviceForm.orderDate,
                    startDate: this.deviceForm.orderDate ? this.deviceForm.orderDate[0] : null,
                    endDate: this.deviceForm.orderDate ? this.deviceForm.orderDate[1] : null,
                    orderPlace: this.deviceForm.orderPlace

                }
            }).then(
                res => {
                    console.log(res)
                    this.tableData = res.data.records
                    this.total = res.data.total
                }
            )
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        reset() {
            this.deviceForm.orderDate = ""
            this.deviceForm.orderPlace = ""
            this.load()
        },
        handleFileUploadSuccess() {
            this.$message.success("上传成功")
            this.load()
        },
        handleSizeChange(pageSize) {
            //   console.log(pageSize)
            this.pageSize = pageSize
            this.load()
        }
    },
};
</script>

